html,
body {
    height: 100%;
}
body {
    /* background-color: #999; */
    background: linear-gradient(#aaa 80%, #333 100%);
    box-shadow: inset 0 0 30rem #6b7884;
}

a {
    text-shadow: 0.1em -0.1em 0.2em rgba(255, 255, 255, 0.5);
}

h1 {
    color: var(--bs-link-color);
    font-family: serif;
    font-weight: 600;
    text-shadow: -0.025em 0.025em 0.08em #fff, -0.05em 0.05em 0.08em black,
        0 0 1em white;
}

td {
    padding: 0.3em;
}

.home-link {
    font-size: 3rem;
}
a.home-link span {
    font-size: 1rem;
    color: gold;
    font-weight: bold;
    text-shadow: -0.1em 0.1em 0.2em #333;
}

.link {
    color: var(--bs-link-color);
    text-decoration: underline;
    text-shadow: 0.1em -0.1em 0.2em #ccc;
    cursor: pointer;
}
.link:hover {
  color: var(--bs-link-hover-color);
}

.racing-stripe {
    background-color: #333;
    width: 100%;
    height: 1rem;
}

.post-panel {
    background-color: #eee;
    border-radius: 0.3rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.3);
    margin: 1rem 0;
    padding: 1rem;
}
